import React from 'react';
import styles from '../styles.module.scss';
const numBg = require('@/assets/img/faqbg.png').default;
function CourseList({title, list}) {
  return (
    <div className={styles['faq-list']}>
      <h1>{title}</h1>
      <ul>
        {list.map((item, index) => (
          <li key={item.id}>
            <p><span style={{backgroundImage: `url(${numBg})`}}>{index + 1}</span>{item.tit}</p>
            <p>{item.cont}</p>
          </li>
        ))}
      </ul>
    </div>
  );
}
export default CourseList;
